.App 
{
  text-align: center;
  overflow: auto;
  background-color: black;
  color: white;
  height: 100%;
}

#Profile
{
  height: 100vh;
  background-image: url("./Assets/BG.jpeg");
  background-size: cover;
  background-repeat: no-repeat;
  text-align: left;
}

#ProfileText
{
  position: relative;
  top:20%;
  left: 10%;
  transform: translate(0%, 20%);
  width: fit-content;
  
}

#Projects
{
  background-color: rgb(7, 7, 7);
  height: 100vh;
}

#ContactIconBar
{
  padding: 30px 5px 10px 5px;
}

.ContactButton
{
    color: white;
    text-decoration: underline;
    border-radius: 20px;
    padding: 5px;
}

.ContactIco
{
  padding: 5px 10px 5px 10px;
    width: 40px;
    height: auto;
    margin: auto;
    vertical-align: middle;
}


.Heading1
{
  font-size: 2em;
  font-weight: bolder;
}

.Heading2
{
  font-size: 1.5em;
  margin-bottom: 20px;
}

.Captions
{
  margin-top: 20px;
  font-size: 1em;
  color: rgb(180, 180, 180);
}

